.index-body{
    display: flex;
    height:100vh;
    background-color: #EFDED6;
}

/* flex container index html */
.flex-container{
    display: flex;
    /* flex-direction: row; */
    justify-content: center;
    align-items: center;
    flex-grow: 1;
    padding-left: 10px;
    padding-right: 10px;
}

.flex-row{
    display: flex;
}


.flex-column{
    flex-grow: 1;
    align-content: center;
}


.flex-column> div{
    text-align: center;
    padding: 10px;
}

.responsive{
    width: 100%;
    height: auto;
}

.responsive{
    width: 100%;
    height: auto;
}

img.responsive{
   max-width: 800px;
}

#primary, #secondary{
    display: flex;
    flex-direction: column;
    border: 4px solid;
    border-radius: 3px;
    margin-left: 10px;
    margin-right: 10px;
    flex-grow: 1;
}

#primary{
flex-grow: 3;
}

#secondary{
flex-grow: 1;
background-color: white;
}